// 
export function shuffle(arr){
  let _arr = arr.slice();
  // arr
  for(let i=0; i<_arr.length; i++){
    // 0  i_arr i 
    let j = getRandomInt(0, i);  // jibug
    let t = _arr[i];
    _arr[i] = _arr[j];
    _arr[j] = t;
  }
    return _arr
}

// 
// func
export function debounce(func, delay){
  let timer;
  return function (...args) {
    if(timer){
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      func.apply(this,args);
    },delay)
  }
}



